<template>
  <div class="home">
    <div class="adiv">
      <van-icon name="arrow-left" class="icon" @click="$router.back()" />
      <h1>发布</h1>
    </div>
    <div class="kongbai"></div>
    <van-cell-group>
      <van-field placeholder="请输入标题" v-model="tit" />
    </van-cell-group>
    <div class="kongbai"></div>
    <van-field
      rows="2"
      v-model="msg"
      autosize
      type="textarea"
      maxlength="50"
      placeholder="请输入内容"
      show-word-limit
    />

    <div class="kongbai"></div>
    <van-field
      readonly
      clickable
      name="picker"
      :value="value"
      label="选择分类名字"
      placeholder="点击选择分类名字"
      @click="showPicker = true"
    />
    <van-popup v-model="showPicker" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns"
        @confirm="onConfirm"
        @cancel="showPicker = false"
      />
    </van-popup>
    <van-uploader
      :after-read="afterRead"
      v-model="fileList"
      multiple
      @click-upload="dian"
    />
    <div class="yes" @click="btn">确定</div>
  </div>
</template>

<script>
import { nanoid } from "nanoid";
import axios from "axios";
import { gettokenAPI, getsendAPI,getcate } from "../api/home";

export default {
  name: "fabu",
  data() {
    return {
      value: "",
      index: "",
      tit: "",
      msg: "",
      nanoid: nanoid(),
      token: "",
      fileList: [],
      url: "",
      loginfo: JSON.parse(sessionStorage.getItem("loginfo")),

      columns: [
      ],
      showPicker: false,
      fenlei:[],
    };
  },
  created () {
    if(this.loginfo==null){
         this.$dialog
        .confirm({
          title: "警告警告··········警告警告",
          message: "臭小子，没有登录还想发布内容？？？？怎么可能呢嘿嘿嘿",
        })
        .then(() => {
          this.$router.push("/login");
        })
        .catch(() => {
          this.$toast("你小子取消是吧，那你别想进去了！！！！");
          this.$notify({
            type: "danger",
            message: "你小子取消是吧，那你别想进去了！！！！",
          });
          this.$router.push("/index");

        });
     }else{
          this.$toast("您已进入发布页，请勿发布不良内容，否则将给予封号处理！！！");
          this.$notify({
            type: "primary",
            message: "您已进入发布页，请勿发布不良内容，否则将给予封号处理！！！",
             duration: 2000,
          });    
     }
    getcate().then((result) => {
      this.fenlei=result.data.data
      for (let index = 0; index < this.fenlei.length; index++) {
     
     this.columns.push(this.fenlei[index].name)
        
      }
    }).catch((err) => {
      
    });
  },
  methods: {
    onConfirm(value, index) {
      this.value = value;
      this.index = index;

      this.showPicker = false;
    },
    dian() {
      gettokenAPI()
        .then((result) => {
          console.log("result: ", result);
          this.token = result.data.token;
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    afterRead(file) {
      let param = new FormData();
      param.append("token", this.token);
      param.append("file", file.file);
      param.append("key", this.nanoid + file.file.name);
      axios
        .post("https://upload-z1.qiniup.com", param, {
          "Content-Type": "multipart/form-data",
        })
        .then((result) => {
          this.url = "http://toutiao.longxiaokj.com/" + result.data.key;
          console.log(
            "first",
            "http://toutiao.longxiaokj.com/" + result.data.key
          );
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
    btn() {
      getsendAPI({
        title: this.tit,
            content: this.msg,
            cate_id: this.fenlei[this.index]._id,
            cate_name:this.value,
            author: this.loginfo.userInfo.nickname,
            author_id:this.loginfo.uid,
        imageSrc: [
         this.url,
        ],
      })
        .then((result) => {
          this.$toast(result.data.msg);
            this.$notify({ type: 'success', message: result.data.msg });
            if(result.data.msg=="成功"){
              this.tit=this.msg=this.value=" "
              this.fileList=[]
                this.$router.push('/index')
            }
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;
  color: aliceblue;
  font-size: 14px;
}

.kongbai {
  width: 100%;
  height: 20px;
  background-color: #f5f7f9;
}

.yes {
  width: 80%;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;
  justify-content: center;
  color: aliceblue;
  font-size: 14px;
  margin: 0 auto;
}
h1 {
  margin-left: calc(50vw - 32px);
  font-size: 16px;
}
.icon {
  font-size: 32px;
}
</style>

